@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;300;400;500;600;700;800&display=swap);
body{background-color:#81a5d5}.login{overflow:hidden;background-color:#fff;padding:40px 30px 30px 30px;border-radius:10px;position:absolute;top:50%;left:50%;width:70%;max-width:400px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);transition:box-shadow 300ms,-webkit-transform 300ms;transition:transform 300ms,box-shadow 300ms;transition:transform 300ms,box-shadow 300ms,-webkit-transform 300ms;box-shadow:1px 1px 10px #305589}.login::before,.login::after{content:"";position:absolute;width:600px;height:600px;border-top-left-radius:40%;border-top-right-radius:45%;border-bottom-left-radius:35%;border-bottom-right-radius:40%;z-index:-1}.login::before{left:50%;bottom:-20%;background-color:#97bcea;opacity:1;-webkit-animation:wawes 6s infinite linear;animation:wawes 6s infinite linear}.login::after{left:5%;bottom:-210%;background-color:#ffdbdc;opacity:.5;-webkit-animation:wawes 7s infinite;animation:wawes 7s infinite}.login>input{display:block;border-radius:5px;font-size:16px;background:#fff;width:100%;border:0;padding:10px 10px;margin:15px -10px}.login>button{cursor:pointer;color:#fff;font-size:16px;text-transform:uppercase;width:80px;border:0;padding:10px 0;margin-top:10px;margin-left:-5px;border-radius:5px;background-color:#305589;transition:background-color 300ms}.login>button:hover{background-color:#3f88e1}::-webkit-input-placeholder{color:#7297ce}:-ms-input-placeholder{color:#7297ce}::placeholder{font-weight:normal;color:#0B102D9D;opacity:1}@-webkit-keyframes wawes{from{-webkit-transform:rotate(0)}to{-webkit-transform:rotate(360deg)}}@keyframes wawes{from{-webkit-transform:rotate(0);transform:rotate(0)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
body{font-family:"JetBrains Mono",monospace}
